li {
    display: inline;
    margin: 20px;
    font-weight: bold;
    font-size: 19px;
    color: rgb(199, 190, 190);
    display: inline;
    margin-right: 20px;
}

li:hover {
    color: white;
}

.NavMenu a {
    text-decoration: none;
}

.NavMenu button {
    float: right;
    margin-top: 2px;
    background: silver;
    border-radius: 6px;
    height: 22px;
    width: 55px;
}

.NavMenu button:hover {
    background: rgb(223, 211, 211);
    font-weight: bold;
}

.NavMenu input {
    margin-left: 418px;
}

ul {
    background-color: rgb(32, 32, 63);
    padding: 25px;
}