body {
  background-color: #2c3347;
  text-align: center;
  color: white;
  height: 200vh;
  width: 215vh;
}

.ProductsPage img {
  width: 180px;
  height: 150px;
  border-radius: 9px;
}

.homeImg {
  width: 200px;
  height: 200px;
}

.imageDisplay {
  width: 200px;
  height: 200px;
}

.list {
  display: inline;
}

.features {
  background: rgb(32, 32, 63);
  margin: 25px;
}

.list {
  margin: 5px;
}

.forminput {
  height: 25px;
  width: 250px;
  border: 1px solid black;
  border-radius: 4px;
}

.login-btn {
  padding: 7px;
  background-color: rgb(36, 151, 145);
  width: 70px;
  border: none;
  border-radius: 4px;
}

.login-btn:hover {
  font-weight: bold;
  background-color: rgb(26, 100, 97);
}

.login-form {
  margin-top: 60px;
  padding: 30px;
  align-content: center;
  box-sizing: content-box;
  display: block;

}

.Createform {
  margin-top: 40px;
  padding: 30px;
  align-content: center;
  box-sizing: content-box;
  display: block;
}

.form-div {
  width: 480px;
  height: 250px;
  padding-top: 70px;
  background: rgb(32, 32, 63);
  border-radius: 10px;
  margin: 0 auto;
}

.create-account-link {
  color: rgb(36, 151, 145);
}

.create-account-link:hover {
  color: rgb(26, 100, 97);
  font-weight: bold;
}

.Createform-div {
  width: 480px;
  height: 330px;
  padding-top: 70px;
  background: rgb(32, 32, 63);
  border-radius: 10px;
  margin: 0 auto;
}

.merchandise-heading {
  letter-spacing: 1px;
}

.auctionPage {
  color: aqua;
  text-decoration: none;
}

.auctionPage:hover {
  color: rgb(17, 141, 141);
  font-weight: bold;
}

.money {
  color: rgb(11, 163, 11);
}

.ProductsPage a {
  text-decoration: none;
  color: silver;
}

.ProductsPage a:hover {
  color: white;
}

.sellPage span {
  color: rgb(36, 187, 187);
  font-weight: bold;
}

.buyANDsell ul {
  margin: 20px;
}

.buyANDsell li {
  margin: 40px;
}

.buyANDsell a {
  text-decoration: none;
}

.buyANDsell body {
  height: 550vh;
}

.home_prod span {
  color: rgb(31, 145, 31);
  font-weight: bold;
}

.home_prod img {
  width: 200px;
  height: 200px;
}

.home_prod button {
  padding: 4px;
  background-color: rgb(36, 151, 145);
  width: 70px;
  border: none;
  border-radius: 4px;
}

.home_prod button:hover {
  background-color: rgb(30, 119, 115);
  color: white;
}

.home_prod a {
  color: rgb(36, 151, 145);
  text-decoration: none;
}

.home_prod a:hover {
  color: rgb(29, 109, 105);
  font-weight: bold;
}