body {
    height: 50vh;
}

.prod_page b {
    color: green;
    letter-spacing: 1px;

}

.prod_page img {
    height: 200px;
    width: 230px;
}

.prod_page a {
    text-decoration: none;
    color: rgb(28, 170, 170);
}

.prod_page a:hover {
    color: rgb(30, 119, 115);
}

.prod_page button {
    padding: 4px;
    background-color: rgb(36, 151, 145);
    width: 70px;
    border: none;
    border-radius: 4px;
}

.prod_page button:hover {
    background-color: rgb(30, 119, 115);
    color: white;
}

.singleAuction b {
    color: green;
}

.singleAuction span {
    font-weight: bold;
}

.singleAuction img {
    height: 200px;
    width: 230px;
}

.singleAuction button {
    padding: 4px;
    background-color: rgb(36, 151, 145);
    width: 70px;
    border: none;
    border-radius: 4px;
}

.singleAuction button:hover {
    background-color: rgb(30, 119, 115);
    color: white;
}

.allAuction a {
    text-decoration: none;
    color: silver;
}

.allAuction a:hover {
    color: white;
}

.singleAuction a {
    color: rgb(36, 151, 145);
    text-decoration: none;
}

.singleAuction a:hover {
    color: rgb(29, 109, 105);
    font-weight: bold;
}

.Categories_page p {
    background: rgb(32, 32, 63);
    padding: 13px;
    font-weight: 900;
    letter-spacing: 1px;
    font-family: sans-serif;
}

.Categories_page input {
    height: 20px;
    width: 200px;
    border: none;
    border-radius: 4px;
}

.Categories_page a {
    text-decoration: none;
    color: silver;
}

.Categories_page a:hover {
    color: white;
}